.resizer-container {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 100vw;
  transform: translateX(0);
  transition: transform .5s;
  transition-timing-function: cubic-bezier(0.77, 0, 0.175, 1);
}
.resizer-button {
  position: fixed;
  z-index: 10;
  top: 20px;
  right: 20px;
  width: 48px;
  height: 48px;
  text-align: center;
  border: none;
  border-radius: 50%;
  background: #000;
  color: #FFF;
  transition: all .5s;
  transition-timing-function: cubic-bezier(0.77, 0, 0.175, 1);
}
.resizer-button:focus {
  outline: none;
}
.resizer-button .show {
  display: inline-block;
}
.resizer-button .hide {
  display: none;
}
.demo-is-opened {
  overflow: hidden;
}
.demo-is-opened .resizer-container {
  transform: translateX(-100vw);
}
.demo-is-opened .resizer-button {
  top: 0;
  right: calc(100vw - 48px);
  color: #000;
  background: #FFF;
}
.demo-is-opened .resizer-button .show {
  display: none;
}
.demo-is-opened .resizer-button .hide {
  display: inline-block;
